.Homepage {
  height: 100vh;
}

.Homepage .container {
  height: 100%;
  max-width: 1300px;
  margin: auto;
  padding: 100px 20px 20px 20px;
  display: grid;
  grid-template-columns: 50% 50%;
  grid-template-rows: 100%;
}

.Homepage-text {
  padding-right: 10px;
}

.Homepage-text-container {
  width: 100%;
  height: 100%;
  padding: 20px;
  background-color: rgba(250, 193, 188, 0.5);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  gap: 30px;
}

.Homepage-text h1 {
  font-size: 50px;
  font-family: "Montserrat", sans-serif;
  color: #161854;
}

.Homepage-text .button {
  display: inline-block;
  background-color: #894357;
  color: #ffffff;
  padding: 8px 30px;
  border-radius: 2em;
  font-family: "Montserrat", sans-serif;
  text-decoration: none;
  transition: all 0.3s ease-in;
}

.Homepage-text .button:hover {
  background-color: #161854;
  box-shadow: 0 0 5px gray;
}

.Homepage-img {
  padding-left: 10px;
}

.Homepage-img img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

@media (max-width: 600px) {
  .Homepage .container {
    padding: 125px 20px 20px 20px;
    grid-template-rows: 50% 50%;
    grid-template-columns: 100%;
  }

  .Homepage-text-container {
    align-items: center;
    gap: 10px;
  }

  .Homepage-text {
    padding-right: 0px;
    padding-bottom: 10px;
  }

  .Homepage-text h1 {
    font-size: 30px;
    text-align: center;
  }

  .Homepage-text .button {
    padding: 8px 15px;
  }

  .Homepage-img {
    padding-left: 0px;
    padding-top: 10px;
  }
}
